import { StyledList, StyledListItem, StyledNavLink, StyledFacebookSvg, StyledTelegramSvg,StyledInstaSvg,StyledSocialList,StyledViberSvg,StyledSocialLink,StyledPhoneSvg,StyledPhoneNumber } from './MobileMenu.styled';
import { useTransition, animated } from '@react-spring/web';

export const MobileMenu = ({handleModal}) => {
    
     const transitions = useTransition(true, {
    from: { opacity: 0, transform: 'translateX(100%)' },
    enter: {
      opacity: 1,
      transform: 'translateX(0)',
      transition: 'all 150ms',
    },
    leave: { opacity: 0, transform: 'translateX(100%)' },
    delay: 250,
  })
    
    return (
         transitions(
      (styles, item) =>
        item && (
                     <animated.div style={{ ...styles, width: '100%', minHeight: '100vh', backgroundColor: 'black' }}>
              <StyledList>
                             <StyledListItem><StyledNavLink to="/" onClick={() => handleModal(false)}></StyledNavLink></StyledListItem>
                             <StyledListItem><StyledNavLink to="/permanent" onClick={()=>handleModal(false)}></StyledNavLink></StyledListItem>
                <StyledListItem><StyledNavLink to="/makeup" onClick={()=>handleModal(false)}></StyledNavLink></StyledListItem>
                <StyledListItem><StyledNavLink to="/eyebrows" onClick={()=>handleModal(false)}> </StyledNavLink></StyledListItem>
                <StyledListItem><StyledNavLink to="/courses" onClick={()=>handleModal(false)}></StyledNavLink></StyledListItem>
                 </StyledList>
                 <StyledSocialList>
                   
                   <li><StyledSocialLink><StyledFacebookSvg /></StyledSocialLink></li>
                   <li><StyledSocialLink><StyledTelegramSvg /></StyledSocialLink></li>
                   <li><StyledSocialLink><StyledInstaSvg /></StyledSocialLink></li>
                   <li><StyledSocialLink><StyledViberSvg/></StyledSocialLink></li>
                 </StyledSocialList>
                 <StyledPhoneNumber><StyledPhoneSvg/> +380508832226</StyledPhoneNumber>
        </animated.div>))
    )
}